@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500;700&display=swap);
header {
  grid-area: header;
  position: relative;
  display: -ms-grid;
  display: grid;
  -ms-grid-columns: .6fr 1fr;
      grid-template-columns: .6fr 1fr;
  width: 100%;
  align-content: center;
  font-size: 1rem;
  transition: opacity .3s ease-in-out;
}

header .logo {
  height: 60px;
  display: flex;
  align-content: center;
  color: #355fda;
  font-weight: bold;
  font-size: 1.4em;
  justify-self: start;
  padding-left: 3em;
}

header .logo p {
  -ms-grid-row-align: center;
  align-self: center;
  margin-left: .4em;
}

header section {
  position: relative;
  align-self: center;
  justify-self: flex-end;
  padding-right: 3em;
  display: flex;
  height: 3em;
  place-content: flex-end;
}

header section .react-date-picker {
  min-width: 12em;
}

header section .react-date-picker .react-date-picker__wrapper {
  border: thin solid #d9dbe0;
  border-radius: .2em;
  padding: 0 .5em;
}

header section .react-date-picker .react-date-picker__wrapper .react-date-picker__inputGroup__input {
  outline: none;
}

header section select {
  min-width: 10em;
  padding-left: 3.8em;
  margin-left: 1em;
  border: thin solid #d9dbe0;
  outline: none;
}

@media screen and (max-width: 480px) {
  header {
    -ms-grid-columns: 1fr;
        grid-template-columns: 1fr;
    -ms-grid-rows: 1fr 1fr;
        grid-template-rows: 1fr 1fr;
    place-items: center;
  }
  header .logo {
    -ms-grid-column-align: center;
        justify-self: center;
    height: 40px;
    padding: 0;
  }
  header section {
    -ms-grid-column-align: center;
        justify-self: center;
    padding: 0;
  }
  header section .react-date-picker__calendar {
    left: 50% !important;
    transform: translateX(-50%);
  }
}

footer {
  grid-area: footer;
  display: -ms-grid;
  display: grid;
  place-items: center;
  font-size: 1rem;
  background-color: #008cff;
  background: linear-gradient(to bottom, #008cff, #0971c5, #0971c5, #008cff);
  position: relative;
  cursor: pointer;
}

footer.tooltip::after {
  content: attr(data-tooltip);
  position: absolute;
  pointer-events: none;
  background: linear-gradient(to bottom, #008cff, #0971c5, #0971c5, #008cff);
  color: #fff;
  text-align: center;
  left: 50%;
  transform: translate(-50%, -50%);
  transition: all .3s ease-in-out;
  opacity: 0;
  font-size: 1.1em;
  padding: 1em;
  border-radius: .5em;
}

footer.tooltip:hover::after {
  transform: translate(-50%, -130%);
  opacity: 1;
}

footer .expand_collapse {
  color: #FFF;
  font-size: 1.4em;
  transition: all .3s ease-in-out;
}

.loading {
  grid-area: body;
  display: -ms-grid;
  display: grid;
  place-items: center;
}

.loading h1 {
  color: #0873c9;
}

*{
  margin: 0;
  padding: 0;
}

html, body{
  height: 100vh;
  width: 100%;  
}

body{
  font-size: 16px;
  font-family: 'Roboto', sans-serif;
}

#root{
  height: 100vh;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 80px 1fr 50px;
  grid-template-areas: 
  'header'
  'body'
  'footer';
}

#root.modal header, #root.modal .body{
  opacity: .1;
}

#root.modal header .react-date-picker, #root.modal .body .card{
  pointer-events: none;
}

@media screen and (max-width: 480px){
  #root{
    grid-template-rows: 160px 1fr 50px;
  }
}

::-webkit-scrollbar{
  display: none;
}
.body {
  grid-area: body;
  position: relative;
  overflow-y: scroll;
  display: -ms-grid;
  display: grid;
  grid-gap: 10px;
  gap: 10px;
  -ms-grid-columns: (minmax(300px, 1fr))[auto-fill];
      grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  grid-auto-rows: 12em;
  transition: opacity .3s ease-in-out;
  padding: 1em;
  padding-top: 3em;
}

.body .card {
  position: relative;
  border: thin solid #9598a0;
  border-radius: 5px;
  padding: 1em;
  display: -ms-inline-grid;
  display: inline-grid;
  -ms-grid-columns: 1fr 1fr;
      grid-template-columns: 1fr 1fr;
  -ms-grid-rows: 0.5fr (1fr)[3];
      grid-template-rows: 0.5fr repeat(3, 1fr);
  grid-gap: 5px;
  gap: 5px;
  box-shadow: 5px -5px 10px rgba(0, 0, 0, 0.05);
  align-items: center;
  height: 10em;
  transition: all .3s ease-in-out;
}

.body .card.base {
  background: linear-gradient(45deg, #bfc1c2, white, #d6d9dd);
}

.body .card:hover {
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
}

.body .card:hover .flag {
  transform: rotate(-15deg);
}

.body .card .close {
  -ms-grid-column: 2;
  -ms-grid-column-span: 1;
  grid-column: 2/3;
  -ms-grid-row: 1;
  -ms-grid-row-span: 1;
  grid-row: 1/2;
  -ms-grid-column-align: end;
      justify-self: end;
  transition: all .3s ease-in-out;
}

.body .card .close:hover {
  cursor: pointer;
  color: red;
}

.body .card .flag {
  -ms-grid-column: 1;
  -ms-grid-column-span: 1;
  grid-column: 1/2;
  -ms-grid-row: 2;
  -ms-grid-row-span: 1;
  grid-row: 2/3;
  box-shadow: 0 -5px 10px rgba(0, 0, 0, 0.16);
  transition: all .3s ease-in-out;
}

.body .card .symbol {
  -ms-grid-column: 1;
  -ms-grid-column-span: 1;
  grid-column: 1/2;
  -ms-grid-row: 2;
  -ms-grid-row-span: 1;
  grid-row: 2/3;
  -ms-grid-column-align: end;
      justify-self: end;
  margin-right: 1em;
}

.body .card .convert {
  position: relative;
  -ms-grid-column: 1;
  -ms-grid-column-span: 2;
  grid-column: 1/3;
  -ms-grid-row: 3;
  -ms-grid-row-span: 1;
  grid-row: 3/4;
  -ms-grid-column-align: end;
      justify-self: end;
  font-size: 1em;
}

.body .card .convert:hover::after {
  opacity: 1;
  transform: translateY(2em);
}

.body .card .convert::after {
  content: attr(data-msg);
  position: absolute;
  text-align: center;
  background-color: #0873c9;
  color: #FFF;
  pointer-events: none;
  opacity: 0;
  transition: all .3s ease-in-out;
  min-width: 100%;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  padding: 5px;
  right: 0;
  transform: translateY(0);
  box-sizing: border-box;
  box-shadow: 0 10px 10px rgba(0, 0, 0, 0.16);
  border-radius: 3px;
}

.body .card .abbreviation {
  -ms-grid-column: 1;
  -ms-grid-column-span: 1;
  grid-column: 1/2;
  -ms-grid-row: 3;
  -ms-grid-row-span: 2;
  grid-row: 3/5;
  -ms-grid-column-align: start;
      justify-self: start;
  border-radius: 50%;
  text-align: center;
  background-color: #0873c9;
  color: #FFF;
  width: 2em;
  height: 2em;
  display: -ms-grid;
  display: grid;
  font-size: .8em;
  padding: 6px;
  place-content: center;
}

.body .card .textbox {
  -ms-grid-column: 2;
  -ms-grid-column-span: 1;
  grid-column: 2/3;
  -ms-grid-row: 2;
  -ms-grid-row-span: 1;
  grid-row: 2/3;
  line-height: 2em;
  width: 10em;
  border: thin solid #d9dbe0;
  text-align: center;
}

.body .card .textbox::-webkit-input-placeholder {
  color: #d9dbe0;
  font-style: italic;
}

.body .card .textbox:-ms-input-placeholder {
  color: #d9dbe0;
  font-style: italic;
}

.body .card .textbox::-ms-input-placeholder {
  color: #d9dbe0;
  font-style: italic;
}

.body .card .textbox::placeholder {
  color: #d9dbe0;
  font-style: italic;
}

.body .card .textbox:hover::after {
  opacity: 1;
  transform: translateY(2em);
}

.body .card .textbox::after {
  content: attr(data-msg);
  position: absolute;
  text-align: center;
  background-color: #0873c9;
  color: #FFF;
  pointer-events: none;
  opacity: 0;
  transition: all .3s ease-in-out;
  min-width: 100%;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  padding: 5px;
  right: 0;
  transform: translateY(0);
  box-sizing: border-box;
  box-shadow: 0 10px 10px rgba(0, 0, 0, 0.16);
  border-radius: 3px;
}

.body .card .name {
  -ms-grid-column: 1;
  -ms-grid-column-span: 2;
  grid-column: 1/3;
  -ms-grid-row: 4;
  -ms-grid-row-span: 1;
  grid-row: 4/5;
  -ms-grid-column-align: end;
      justify-self: end;
  font-size: .8em;
  font-weight: bold;
  color: #0873c9;
}

